div.skillcard {
	margin: 1vw 1vw;
	display: flex;
	flex: 1 0 auto;
	min-width: 15%;
	max-width: 25%;
	height: calc(30px + 1.8vw);
	flex-direction: row;
	align-items: center;
	padding: 1vw 0;
}
div.skillcard div.skillcard-icon {
	padding: 0 1vw;
}
div.skillcard div.skillcard-icon img {
	height: calc(10px + 1.3vw);
	width: calc(10px + 1.5vw);
}
div.skillcard p {
	font-size: 1.2vw;
	vertical-align: middle;
	margin: 0 0;
}
