section.fourdmentorship-section {
	margin: 6vw 5vw 3vw;
	height: 40vw;
}

section.fourdmentorship-section main.fourdmentorship-section {
	margin: 0;
}
section.mentors-mob.mob {
	display: none;
}

main.fourdmentorship-section h2.fourdmentorship-heading {
	font-size: var(--webHeading);
	font-weight: 700;
	margin-bottom: 2vw;
}

p.fourdmentorship-desc {
	width: 35%;
	font-size: var(--webSmallFont);
	line-height: 1.6em;
}
p.fourdmentorship-desc.mob {
	display: none;
}
svg.connecting-lines {
	position: absolute;
	width: 100%;
	height: 50vw;
	margin: -5vw -4vw;
}

.line {
	position: absolute;
	width: 2px;
	margin-top: -1px;
	background-color: var(--baseBlack);
}

/* chart */
div.infographic-nodes {
	position: absolute;
	display: flex;
	align-items: center;
	font-family: 'Poppins';
	color: gray;
	background-image: linear-gradient(to right, #e9f8ff, white);
	height: 8vw;
}

div.infographic-nodes h2 {
	font-size: 1.2vw;
	font-weight: 700;
	margin: 0;
	padding: 0.3vw 0 0 0.6vw;
	white-space: nowrap;
}

div.infographic-nodes p {
	font-size: 0.9vw;
	margin: 0;
	padding: 0.3vw 0 0 0.6vw;
}

div.infographic-nodes img {
	border-radius: 100%;
	border: solid 3px var(--baseOrange);
	background-color: var(--baseWhite);
	height: 9vw;
	box-shadow: 0px 2px 8px var(--shade2Orange);
}

section.infographic-image p.infographic-image-caption {
	height: 0;
	width: 0;
	white-space: nowrap;
	font-size: 1.1vw;
	color: var(--baseOrange);
	font-weight: 600;
}

section.infographic-image p.infographic-image-caption.inverted {
	line-height: 0;
	padding-bottom: 0.7vw !important;
}

/* supermentor node */
div#supermentor-node {
	background-image: linear-gradient(to left, #e9f8ff, white);
}

div#supermentor-node h2 {
	padding: 0.3vw 0.6vw 0 0.6vw;
}

div#supermentor-node p {
	padding: 0.3vw 0.6vw 0 0.6vw;
}

/* root node */
img#root-node {
	position: absolute;
	height: 7vw;
	margin-top: -7vw;
	margin-left: 60vw;
	border-radius: 50%;
	box-shadow: 0px 2px 16px var(--shade2Orange);
}

/* tripmentor node */
div#tripmentor-node {
	margin-top: 10vw;
	margin-left: 67vw;
}

/* industrymentor node */
div#industrymentor-node {
	margin-top: 10vw;
	margin-left: 17vw;
}

/* personalmentor node */
div#personalmentor-node {
	margin-top: -20vw;
	margin-left: 31vw;
}

/* supermentor node */
div#supermentor-node {
	margin-top: -20vw;
	margin-left: 66vw;
}
