main.lifeskills-section img.lifeskills-bg {
	width: 100%;
}

main.lifeskills-section section.lifeskills-content {
	position: absolute;
	margin: 9vw 5vw 5vw;
	font-family: 'Poppins';
}

.ben-lifeskills-head {
	font-size: var(--webHeading);
	font-weight: 700;
}

.ben-lifeskills-about {
	font-size: var(--webNormalFont);
	margin-top: 3vw;
	width: 62%;
}

section.lifeskills-content section.lifeskills-intro {
	display: flex;
}

section.lifeskills-intro div.lifeskills-intro-icon {
	flex: 1 0 30vw;
}

section.lifeskills-intro section.lifeskills-intro-text {
	flex: 1 1 50vw;
}

div.lifeskills-intro-icon img.lifeskills-intro-icon {
	width: 25vw;
}

section.lifeskills-cards-section {
	display: flex;
	flex-direction: column;
	justify-content: space-around;
	margin-top: 1.5vw;
}

section.lifeskills-cards-section div.lifeskills-cards {
	display: flex;
	flex-wrap: wrap;
	justify-content: space-between;
}

section.lifeskills-cards-section div.lifeskills-cards-mob,
section.lifeskills-cards-section h3.heading-mob {
	display: none;
}

section.lifeskills-cards-section div.mobile-slider-container {
	display: flex;
	flex-direction: row;
	margin: 2vw 0;
}

section.lifeskills-cards-section div.lifeskills-cards::before {
	content: '10 SOFT SKILLS';
	margin-top: 3vw;
	margin-left: 1vw;
	margin-bottom: 3vw;
	font-size: 1.3vw;
	font-weight: 700;
	font-family: 'Poppins';
	color: gray;
	margin-right: 100%;
	white-space: nowrap;
}

div.mobile-slider-container div.lifeskills-cards-mob::before {
	content: ' ';
}

section.lifeskills-cards-section div.lifeskills-cards.career::before {
	content: '14 CAREER ROLES';
}

section.lifeskills-cards-section div.lifeskills-cards::after {
	content: '';
	flex: auto;
}
/* hidden mobile view */
main.lifeskills-section section.lifeskills-content div.skills {
	display: none;
}
