main.fivex-growth {
	display: flex;
	justify-content: center;
}
main.fivex-growth section.fivex-content {
	position: absolute;
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	margin: 5vw 3vw;
	width: 90%;
	height: 40vw;
	font-family: 'Poppins';
}
main.fivex-growth section.fivex-heading {
	display: flex;
	justify-content: space-between;
}
section.fivex-heading h1 {
	margin-top: 3vw;
	font-size: var(--webHeading);
	font-weight: 700;
}
section.fivex-heading p {
	margin-top: 3vw;
	width: 70%;
	font-size: var(--webNormalFont);
}
main.fivex-growth section.fivex-info {
	display: flex;
	justify-content: space-between;
	font-size: 1.2vw;
}
section.fivex-info div.card {
	display: flex;
	flex: 1 0 0;
	margin: 0 0.6vw;
	padding: 2vw 0;
}
img.section-icon {
	max-height: 15vw;
}
img.icon-small {
	width: 10vw;
}
img.fivex-bg-img {
	width: 100%;
}
