main.benefits-intro {
    padding: 10vw 5vw;
    color: var(--baseWhite);
    background: var(--baseBlue);
    display: flex;
    font-family: 'Poppins';
}

main.benefits-intro main.benefits-desc {
    flex: 0 1 50%;
}

main.benefits-desc h1 {
    font-weight: 700;
    font-size: var(--webHeading);
    margin-bottom: 3vh;
}

main.benefits-desc h6 {
    font-size: var(--webSmallFont3);
    margin-top: 3vh;
    font-weight: 600;
}

main.benefits-desc p {
    width: 82%;
    font-size: var(--webNormalFont);
    color: var(--shade6White);
}

h1.benefits-explore-dream-heading {
    margin-bottom: 4vw;
}

p#tc {
    font-size: 0.7em;
    width: 75%;
}

div.benefits-vertical-sectioning {
    margin: 0 1vw;
    width: 0;
    height: auto;
    border-right: solid 2px #13212d;
}

section.offer-cards {
    display: flex;
    justify-content: center;
    flex-direction: column;
    padding: 0 3vw;
    width: 32vw;
}

section.offer-cards p {
    font-size: var(--webSmallFont2);
}

div.offer-card {
    background: #13212d;
    margin: 1.5vw 0;
    padding: calc(0.3em + 1.4vw) calc(0.3em + 2.2vw);
    border-radius: 1vw;
}

div.offer-card p {
    margin: 0;
}

div.offer-card p.inline-pricing-text {
    font-size: calc(0.5em + 0.8vw);
}

div.offer-card p.inline-pricing-text span {
    font-size: calc(1.5em + 0.8vw);
}

div.offer-card h6 {
    font-size: calc(0.6em + 0.5vw);
    margin: 0;
}

div.offer-card p.interested-pricing-heading {
    color: gray;
    font-weight: 700;
    font-size: calc(0.6em + 0.5vw);
}

.pricespace {
    margin-right: 2vw;
}

.insurancetag {
    width: 11vw;
}